.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

#content {
  border-width: 2px;
  border-style: solid;
  padding: 10px;
}

.active {
  border: 2px solid black;
}

.container {
  padding: 20px;
  margin-top: 100px;
  border-radius: 20px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #ffff;
}

.task-form {
  transition: all ease-out .4s;
}

.task-item:hover {
  outline: 2px solid lightblue;
  transition: all ease-out .4s;
  color: darkgrey!important;
}

.status-item:hover {
  transform: scale(1.2);
  transition: all ease-out .4s;
}
.sort_selected::after {
  content: 'checked';
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  margin: 0px 0px 0px 10px;
  display: inline-block;
}

.item-active {
  border: 2px solid #17a2b8;
  border-radius: 10px;
}

.item-hidden {
  border: 2px solid #6c757d;
  border-radius: 10px;
}

.search-input {
  background: #1a1a1a!important;
  border: 2px solid #adb5bd;
  color: #ffff!important;
}

.filter-control {
  background: #1a1a1a;
  outline: none;
  padding: 10px;
  color: #ffff;
  border: 1px solid #adb5bd;
}

.remove-item {
  text-decoration: line-through;
  color: #a9a9a96e!important;
}

.active-item {
  color: #ffff;
}

.update-form {
  background: #1a1a1a!important;
  border: 2px solid #4f8292;
  color: #4f8292!important;
}

.update-btn {
  border: 2px solid #6a9072;
  color: #6a9072;
}

.add-btn {
  border: 2px solid #7b86ae;
  color: #7b86ae
}
